@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
*{
    margin: 0;
    padding: 0;
}

.List{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.Title{
    font-family: 'Bebas Neue', cursive;
    text-align: center;
    color: #232323;
    font-size: calc(10px + 5vmin);
}

.ImgList{
    background-color: rgba(221, 215, 209, 0.445);
    border-radius: 20px;
    padding: 40px;
    font-family: 'Bebas Neue', cursive;
    text-align: center;
    font-size: 25px;
    display: grid;
    margin-left: 40px;
    margin-right: 40px;
    max-width: 100vw;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 1vw;
    grid-row-gap: 50px;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

.imgl{
    margin-bottom: 15px !important;
    width: 250px;
    height: 250px;
    border-radius:20%;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

.on { grid-area: 1 / 1 / 2 / 2; 
    margin: auto;
    display: block;}
.tw { grid-area: 1 / 2 / 2 / 3; 
    margin: auto;
    display: block;}
.th { grid-area: 1 / 3 / 2 / 4; 
    margin: auto;
    display: block;}
.fo { grid-area: 1 / 4 / 2 / 5; 
    margin: auto;
    display: block;}
.fi { grid-area: 1 / 5 / 2 / 6; 
    margin: auto;
    display: block;}
.si { grid-area: 2 / 1 / 3 / 2; 
    margin: auto;
    display: block;}
.se { grid-area: 2 / 2 / 3 / 3; 
    margin: auto;
    display: block;}
.ei { grid-area: 2 / 3 / 3 / 4; 
    margin: auto;
    display: block;}
.ni { grid-area: 2 / 4 / 3 / 5; 
    margin: auto;
    display: block;}
.te { grid-area: 2 / 5 / 3 / 6; 
    margin: auto;
    display: block;}

    
    a.animated-button:link, a.animated-button:visited {
        padding: 15px;
        position: relative;
        display: block;
        font-family: 'Bebas Neue', cursive;
        color: #232323;
        font-size:14px;
        font-weight: bold;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        overflow: hidden;
        letter-spacing: .08em;
        border-radius: 0;
        text-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.2);
        -webkit-transition: all 1s ease;
        -moz-transition: all 1s ease;
        -o-transition: all 1s ease;
        transition: all 1s ease;
    }
    a.animated-button:link:after, a.animated-button:visited:after {
        content: "";
        position: absolute;
        height: 0%;
        left: 50%;
        top: 50%;
        width: 150%;
        z-index: -1;
        -webkit-transition: all 0.75s ease 0s;
        -moz-transition: all 0.75s ease 0s;
        -o-transition: all 0.75s ease 0s;
        transition: all 0.75s ease 0s;
    }
    a.animated-button:link:hover, a.animated-button:visited:hover {
        color: #232323;
        text-shadow: none;
    }
    a.animated-button:link:hover:after, a.animated-button:visited:hover:after {
        height: 450%;
    }
    a.animated-button:link, a.animated-button:visited {
        position: relative;
        display: block;
        color: #232323;
        font-size:20px;
        border-radius: 0;
        font-weight: bold;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        overflow: hidden;
        letter-spacing: .08em;
        text-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.2);
        -webkit-transition: all 1s ease;
        -moz-transition: all 1s ease;
        -o-transition: all 1s ease;
        transition: all 1s ease;
    }

    

a.animated-button.thar-three {
        color: #232323;
        cursor: pointer;
        display: block;
        position: relative;
        border: 2px solid #00FFCB;
        transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);
        box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);

    }
    a.animated-button.thar-three:hover {
        color: #E5E5E5 !important;
        background-color: transparent;
        text-shadow: nthree;
    }
    a.animated-button.thar-three:hover:before {
        left: 0%;
        right: auto;
        width: 100%;
    }
    a.animated-button.thar-three:before {
        display: block;
        position: absolute;
        top: 0px;
        right: 0px;
        height: 100%;
        width: 0px;
        z-index: -1;
        content: '';
        color: #000 !important;
        background: #4FD1C5;
        transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);

}

