.App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-between;
    
}

.App-header::before{
    content: "";
    background-image: url("../../Assests/Cover.jpg");
    background-size: cover;
