html, body {
    height: 100%;
  }
  
  .wrap {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .buttonHome {
    min-width: 300px;
    min-height: 60px;
    font-family: 'Bebas Neue', cursive;
    font-size: 22px;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    font-weight: 700;
    color: #313133;
    background: #4FD1C5;
  background: linear-gradient(90deg, rgba(129,230,217,1) 0%, rgba(79,209,197,1) 100%);
    border: none;
    border-radius: 1000px;
    box-shadow: 12px 12px 24px rgba(79,209,197,.64);
    transition: all 0.3s ease-in-out 0s;
    cursor: pointer;
    outline: none;
    position: relative;
    padding: 10px;
    }
  
  .buttonHome::before {
    content: '';
    border-radius: 1000px;
    min-width: calc(300px + 12px);
    min-height: calc(60px + 12px);
    border: 6px solid #00FFCB;
    box-shadow: 0 0 60px rgba(0,255,203,.64);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: all .3s ease-in-out 0s;
  }
  
  .buttonHome:hover, .buttonHome:focus {
    color: #313133;
    transform: translateY(-6px);
  }
  
  .buttonHome:hover::before, .buttonHome:focus::before {
    opacity: 1;
  }
  
  .buttonHome::after {
    content: '';
    width: 30px; height: 30px;
    border-radius: 100%;
    border: 6px solid #00FFCB;
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: ring 1.5s infinite;
  }
  
  .buttonHome:hover::after, .buttonHome:focus::after {
    animation: none;
    display: none;
  }

  .up{
    position: sticky;
    top: 94%;
    left: 97%;
    width: 50px;
    height: 50px;
    background-color: #00FFCB;
    border: 1px solid white;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.64);
    border-radius: 50%;
    color:aliceblue;
    text-align: center;
    font-size: 40px;
    cursor: pointer;
  }
  
  @keyframes ring {
    0% {
      width: 30px;
      height: 30px;
      opacity: 1;
    }
    100% {
      width: 300px;
      height: 300px;
      opacity: 0;
    }
  }

  