.btnn {
    box-sizing: border-box;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background-color: transparent;
    border: 2px solid #e74c3c;
    border-radius: 0.6em;
    color: #e74c3c;
    cursor: pointer;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-self: center;
        -ms-flex-item-align: center;
            align-self: center;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1;
    margin: 20px;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
  }
  .btnn:hover, .btnn:focus {
    color: #fff;
    outline: 0;
  }

  .third {
    border-color: #3498db;
    color: #fff;
    box-shadow: 0 0 40px 40px #3498db inset, 0 0 0 0 #3498db;
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
  }
  .third:hover {
    box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
  }

  .Referencias{
      min-width: 80vw;
      min-height: 60vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: end;
  }

  a{
      color: white;
      text-decoration: none;
      font-family: 'Bebas Neue', cursive;
    font-size: 24px;
  }

  a:hover{
    color: #3498db;
    font-family: 'Bebas Neue', cursive;
    font-size: 24px;
}
  
  
